<template>
  <v-container grid-list-md text-xs-center>
    <v-flex xs12>
      <v-alert outline color="info" icon="info" :value="true">
        Choose challenge category you want to practice
      </v-alert>
    </v-flex>
    <v-layout row wrap>
      <v-flex xs6  v-for="(item, i) in items" :key="i">
        <v-list two-line>
          <v-list-tile @click="goToAccordingUri(item.title)">
            <v-list-tile-content @click="show = !show">
              <v-list-tile-title v-html="item.title"></v-list-tile-title>
              <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
  export default {
    name: "LandingPage",
    data() {
      return {
        show: true,
        items: [
          {
            title: "String",
            subtitle: "String manipulation with loops"
          },
          {
            title: "Array",
            subtitle: "Array manipulation with loops"
          },
          {
            title: "Algorithms",
            subtitle: "Usage of algorithms"
          },
          {
            title: "Functional",
            subtitle: "Lambda expressions"
          },
          {
            title: "Logic",
            subtitle: "Boolean logic puzzles"
          },
          {
            title: "Recursion",
            subtitle: "Problems with functions that call themselves"
          }
        ]
      }
    },
    methods: {
      goToAccordingUri(title) {
        let uri = "/" + title.toLowerCase();
        this.$router.push(uri);
      }

    },
    created: function () {

    }
  }
</script>

<style scoped></style>



